<template>
  <div class="top">
    <!--  -->
    <div class="topMenu">
      <span class="welcome"></span>
      <span class="login" v-show="!username" @click="showLogin"></span>
      <router-link tag="span" to="/Contact/InforList" class="login" v-show="username" >{{username}}</router-link>
      <span class="language"><span>English</span>/<span class="active"></span></span>
    </div>
    <div v-show="loginFlag" class="loginBg">
      <div class="loginBox">
        <div class="loginText"></div>
        <div class="loginTel">
          <span></span><input type="text" v-model="userTel" name="" placeholder="">
        </div>
        <div class="loginPwd">
          <span></span><input type="password" v-model="userPwd" name="" placeholder="">
        </div>
        <div class="loginBtn" @click="login">
          
        </div>
        <div class="closeLogin" @click="closeLogin">
          <img src="../../assets/images/closeLogin.png">
        </div>
      </div>
    </div>
    <!--  -->
    <div v-bind:class="bgColor ==='white'?'topNavBoxWhite':'topNavBox'">
      <router-link to="/" tag="div" class="topNavLeft">
        <img v-if="bgColor == 'white'" src="../../assets/images/logo.png"/>
        <img v-else src="../../assets/images/logoBlank.png"/>
      </router-link>
      <!-- TopNav.vue -->
      <ul class="topNav">
        <router-link to="/" tag="li" exact ></router-link>
        <router-link to="/AboutUs" tag="li" exact ></router-link>
        <router-link to="/Funding" tag="li" exact ></router-link>
        <router-link to="/Team" style="display:none" tag="li" >/</router-link>
        <router-link to="/Coverage" tag="li" exact ></router-link>
        <router-link to="/Blog" style="display:none" tag="li" ></router-link>
        <router-link to="/Scenarios" tag="li" exact ></router-link>
        <router-link to="/Contact" tag="li" ></router-link>
      </ul>
    </div>  
  </div>
  
</template>

<script>
import store from '@/vuex/store';
import {mapState,mapMutations} from 'vuex';

export default {
  name: 'TopNavBlack',
  store,
  data () {
    return {
    }
  },
  props: ['bgColor'],
  computed: {
    ...mapState(['loginFlag','userTel','userPwd','username','ownUri']),
    userTel : {
      get () {
        return this.$store.state.userTel
      },
      set (value) {
        this.$store.commit('updateUserTel', value)
      }
    },
    userPwd : {
      get () {
        return this.$store.state.userPwd
      },
      set (value) {
        this.$store.commit('updateUserPwd', value)
      }
    },
  },
  methods: {
    ...mapMutations(['showLogin','closeLogin','login']),
    init:function(){
      // console.log(this.bgColor);
    }
  },
  mounted: function(){
    this.init();
  },
  beforeRouteEnter (to, from, next) {
    //  confirm 
    //  `this`
    // 
    console.log(to);
  },
  beforeRouteUpdate (to, from, next) {
    // 
    //  /foo/:id /foo/1  /foo/2 
    //  Foo 
    //  `this`
    console.log(from);
  },
  beforeRouteLeave (to, from, next) {
    // 
    //  `this`
    console.log(next);
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/**/
.topMenu{
  width: 100%;
  height: 26px;
  background: #f0f0f0;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
}
.topMenu span{
  height: 26px;
  display: inline-block;
  line-height: 26px;
}
.welcome{
  margin-left: 5%;
  font-family: 'Bold';
  font-size: 12px;
  color: #333;
}
.login{
  margin-left: 2.5%;
  font-family: 'Normal';
  font-size: 12px;
  color: #c49a6d;
  cursor: pointer;
}
.language{
  margin-left: 63%;
  font-family: 'Normal';
  font-size: 12px;
  color: #333;
}
.language span{
  margin: 0 10px;
  cursor: pointer;
}
.language .active{
  color: #c49a6d;
}

/**/
.loginBg{
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
  background: transparent;
  z-index: 11;
}
.loginBox{
  width: 285px;
  height: 240px;
  background: rgba(240,240,240,0.9);
  position: absolute;
  left: 50%;
  margin-left: -142.5px;
  top: 50%;
  margin-top: -130px;
  padding: 25px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 0 0 14px 7px #ddd;
}
.loginBox div{
  width: 225px;
  height: 32px;
  margin-bottom: 20px;
  line-height: 32px;
  text-align: center;
}
.loginText{
  font-family: 'Regular';
  font-size: 21px;
  color: #333;
}
.loginTel{
  width: 100%;
  height: 32px;
  background: #fff;
  font-family: 'Normal';
  font-size: 15px;
  color: #4d4d4d;
}
.loginTel span{
  display: inline-block;
  width: 25%;
}
.loginPwd{
  width: 100%;
  height: 32px;
  background: #fff;
  font-family: 'Normal';
  font-size: 14px;
  color: #979797;
}
.loginPwd span{
  display: inline-block;
  width: 25%;
}
.loginTel input{
  width: 70%;
  height: 30px;
  line-height: 30px;
  border: none;
  background: #fff;
  font-family: 'Normal';
  font-size: 14px;
  color: #4d4d4d;
}
.loginPwd input{
  width: 70%;
  height: 28px;
  line-height: 28px;
  border: none;
  background: #fff;
  font-family: 'Normal';
  font-size: 13px;
  color: #979797;
}
.loginBtn{
  width: 100%;
  height: 38px;
  line-height: 38px;
  background: #c49a6d;
  border-radius: 5px;
  color: #fff;
  font-family: 'Normal';
  font-size: 14px;
  cursor: pointer;
}
.loginBox div.closeLogin{
  width: 16px;
  height: 16px;
  position: absolute;
  right: 20px;
  top: 20px;
}
.loginBox div.closeLogin img{
  width: 100%;
  cursor: pointer;
}
/**/
.topNavBoxWhite{
  width: 100%;
  height: 89px;
  background: #fff;
  position: fixed;
  top: 26px;
  left: 0;
  z-index: 10;
}
.topNavBox{
  width: 100%;
  height: 89px;
  background: rgba(0,0,0,0.7);
  position: fixed;
  top: 26px;
  left: 0;
  z-index: 10;
}
.topNavLeft{
  width: 20%;
  height: 85px;
  float: left;
  margin-left: 8%;
  margin-right: 4%;
  cursor: pointer;
}
.topNavLeft img{
  height: 51px;
  display: block;
  margin-top: 17px; 
}
.topNav{
  width: 66%;
  float: left;
  padding-top: 20px;
  line-height: 65px;
}
.topNav li{
  float: left;
  width: 9%;
  margin: 0 1%;
  font-family: 'Normal';
  font-size: 14px;
  border-bottom: 2px solid transparent;
  text-align: center;
  cursor: pointer;
  color: #fff;
}
.topNavBoxWhite li{
  color: #333;
}
.topNav li.active{
  color: #c49a6d;
  border-bottom: 4px solid #c49a6d;
}
</style>


