<template>
  <div class="aboutUs">
    <!-- , -->
    <TopNavBlack></TopNavBlack>
    <!--  -->
    <div style="width:100%;height:115px;background:transparent"></div>
    <div id="contact">
      <!--  -->
      <div class="aboutImg">
        <img src="../../assets/images/banner2.png">
        <div class="aboutImgBox">
          <div class="aboutChi"></div>
          <div class="aboutEng">CONTACT US</div>
        </div>
        
      </div>
      <!--  -->
      <div class="contactBox">
        <div class="contactLeft">
          <div class="leftTitle"></div>
          <div class="leftTitleEng">Contact Information</div>
          <div class="leftMessage"><br/>business@green-stone.cn</div>
          <div class="leftQrcode"><img src="../../assets/images/qrcode.png"></div>
        </div>
        <div class="contactRight">
          <div class="rightTitle"></div>
          <div class="rightInput">
            <input type="text" v-model.trim="clientName" name="" placeholder="">
          </div>
          <div class="rightInput">
            <input type="text" v-model="clientTel" name="" placeholder="">
          </div>
          <div class="rightInput">
            <input type="text" v-model="clientEmail" name="" placeholder="">
          </div>
          <div class="rightInput">
            <input type="text" v-model.trim="clientSubject" name="" placeholder="">
          </div>
          <div class="rightTextarea">
            <textarea cols="5" rows="3" v-model.trim="clientDemand" placeholder="">
              
            </textarea>
          </div>
          <div class="rightButton" @click="submit"></div>
        </div>
      </div>  
    </div>
    
    <!--  -->
    <Bottom></Bottom>
  </div>
</template>

<script>
import TopNavBlack from '@/components/common/TopNavBlack';
import Bottom from '@/components/common/Bottom';
import store from '@/vuex/store';
import {mapState} from 'vuex';
import axios from 'axios';
export default {
  name: 'ContactUs',
  store,
  data () {
    return {
      clientName: '',
      clientTel: '',
      clientEmail: '',
      clientSubject: '',
      clientDemand: ''
    }
  },
  computed:{
    ...mapState(['uesrname'])
  },
  methods: {
    init: function(){
      console.log(this.username);
    },
    submit: function(event) {
      if(!this.clientName){
        alert('');
        return;
      }else if(!this.clientTel){
        alert('');
        return;
      }else if(this.clientTel.length != 11){
        alert('');
        return;
      }else if(!this.clientSubject){
        alert('');
        return;
      }else if(!this.clientDemand){
        alert('');
        return;
      }
      // lfdid:ID      int
      // dName:       string
      // dMobile:     string
      // dMail:     string
      // dStandard:    string
      // ddesc:     string  
      // dReply:   0   1     int
      var that = this;
      var url = '';
      var str = window.location.origin;
      if(str.indexOf('localhost')>-1){
        url = 'http://www.lvshikaimen.com'
      }else{
        url = window.location.origin
      }
      axios.post(url + '/exp/UpdatelfDemand.do', {
        "lfdid": 0,
        "dName": that.clientName,
        "dMobile": that.clientTel,
        "dMail": that.clientEmail,
        "dStandard": that.clientSubject,
        "ddesc": that.clientDemand,
        "dReply": 0
      })
      .then(function (success) {
        // console.log(success);
        alert('')
      })
      .catch(function (error) {
        alert('');
      });

    }
  },
  
  mounted:function(){
    this.init();
  },
  components:{
    TopNavBlack,
    Bottom
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
/**/
.aboutImg{
  width: 100%;
  position: relative;
  margin-bottom: -89px;
}
.aboutImg img{
  width: 100%;
  position: relative;
  top: -89px;
}
.aboutImgBox{
  width: 100px;
  height: 60px;
  position: absolute;
  left: 50%;
  top: 40%;
  margin-left: -50px;
}
.aboutChi{
  width: 120px;
  height: 30px;
  text-align: center;
  font-family: 'Medium';
  color: #fff;
  font-size: 22px;
  border-bottom: 1px solid #c49a6d;
}
.aboutEng{
  width: 120px;
  height: 22px;
  text-align: center;
  font-family: 'Regular';
  color: #fff;
  font-size: 16px;
}
/**/
.contactBox{
  width: 800px;
  height: 500px;
  margin: 0 auto;
  padding-top: 45px;
}
.contactLeft{
  width: 200px;
  float: left;
  margin-right: 100px;
}
.leftTitle{
  font-family: 'Medium';
  color: #333;
  font-size: 23px;
}
.leftTitleEng{
  font-family: 'Normal';
  color: #c49a6d;
  font-size: 17px;
  margin-bottom: 25px;
}
.leftMessage{
  font-family: 'Normal';
  color: #333;
  font-size: 15px;
  line-height: 2;
  margin-bottom: 25px;
}
.leftQrcode{
  width: 110px;
}
.leftQrcode img{
  width: 110px;
}
.contactRight{
  width: 500px;
  float: left;
}
.rightTitle{
  font-family: 'Medium';
  color: #333;
  font-size: 21px;
}
.rightInput{
  width: 480px;
  height: 29px;
  border: 1px solid #ccc;
  margin: 15px 0;
}
.rightInput input{
  border: none;
  width: 478px;
  height: 27px;
  line-height: 28px;
  font-family: 'Normal';
  color: #979797;
  font-size: 14px;
  text-indent: 10px;
}
.rightTextarea{
  width: 480px;
  height: 170px;
  border: 1px solid #ccc;
  margin: 15px 0;
}
.rightTextarea textarea{
  border: none;
  resize: none;
  width: 476px;
  height: 166px;
  line-height: 22px;
  font-family: 'Normal';
  color: #979797;
  text-indent: 10px;
  font-size: 14px;
}
.rightButton{
  width: 115px;
  height: 35px;
  border: 1px solid #c49a6d;
  text-align: center;
  line-height: 35px;
  font-family: 'Normal';
  color: #c49a6d;
  font-size: 15px;
  background: #fff;
  cursor: pointer;
  border-radius: 5px;
}
</style>



